import type { IScrum } from '@interfaces/scrum'
import request from '@utils/request'

export const getProjectScrums = (params = {}) => {
  return request({
    url: '/scrums',
    method: 'get',
    params,
  })
}

export const createScrum = (data: IScrum) => {
  return request({
    url: '/scrums',
    method: 'post',
    data,
  })
}

export const updateScrum = (_id, data: IScrum) => {
  return request({
    url: `/scrums/${_id}`,
    method: 'put',
    data,
  })
}

export const getScrumDetail = (_id, params = {}) => {
  return request({
    url: `/scrums/${_id}`,
    method: 'get',
    params,
  })
}

export const deleteScrum = (_id) => {
  return request({
    url: `/scrums/${_id}`,
    method: 'delete',
  })
}
